<template>
  <div id="study">

    <div class="study-video" v-if="studyList.media_type=='text'" style="width: 100%;height: 215px;">
      <img :src="videoSrc"/>
    </div>
    <div class="study-video" v-if="studyList.media_type=='video'">
      <video :poster="studyList.media_path" style="width: 100%;height: 215px;" autoplay="autoplay" controls>
        <source :src="studyList.media_path" type="video/mp4"/>
        <source :src="studyList.media_path" type="video/webM"/>
        <source :src="studyList.media_path" type="video/ogg"/>
      </video>
    </div>
    <div class="study-video" v-if="studyList.media_type=='audio'" style="width: 100%;height: 215px;">
      <audio :src="studyList.media_path" autoplay="autoplay" controls></audio>
    </div>
    <div class="list-title">
      <div class="list-china flex-container flex-jus-sb" v-if="studyList.media_type == 'audio' ? false : true">
        <div class="list-wh">
          <h2 class="font18">{{studyList.title}}</h2>
          <span class="iconfont icon-ren font12 color-99 flex-container flex-jus-s align-items-cen"><em
            class="padd-l-20">{{studyList.study_num}}人已学习</em></span>
        </div>
      </div>
      <div class="list-chapter">
        <div class="chapter-title font15 flex-container flex-jus-s padd-b-10">正文</div>
        <ul class="chapter-ul">
          <li class="chapter-ul-li">
            <!-- <div class="study-ya flex-container flex-jus-s font14 align-items-cen padd-b-10"><i class="study-yuan"></i><i class="padd-l-5">{{studyList.title}}</i></div> -->
            <div class="chapter-img" v-html="studyList.content">
            </div>
          </li>
        </ul>
      </div>
    </div>


    <van-tabbar v-model="active">
      <van-tabbar-item @click="geSt">
        <span>学习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active : icon.normal"
        >
      </van-tabbar-item>
      <van-tabbar-item @click="goSo" v-show="exerciseList"><span>练习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active1 : icon.normal1"
        >
      </van-tabbar-item>
      <van-tabbar-item @click="goTe" v-show="testList"><span>测验</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active2 : icon.normal2"
        >
      </van-tabbar-item>
    </van-tabbar>


  </div>
</template>

<script>
  import videoSrc from '@/assets/images/58cb99771007c.jpg'
  import posSrc from '@/assets/images/shipin_zanting.png'
  import study from '@/assets/images/xuexi_1.png'
  import study_active from '@/assets/images/xuexi.png'
  import practice from '@/assets/images/lianxi_1.png'
  import practice_active from '@/assets/images/lainxi.png'
  import test from '@/assets/images/ceyan_1.png'
  import test_active from '@/assets/images/ceyan.png'
  import Vue from 'vue'
  import {Tabbar, TabbarItem, Toast} from 'vant'

  Vue.use(Tabbar).use(TabbarItem).use(Toast);
  export default {
    name: 'study',
    data() {
      return {
        active: 0,
        icon: {
          normal: study,
          active: study_active,//自定义图标1
          normal1: practice,
          active1: practice_active,//自定义图标2
          normal2: test,
          active2: test_active,//自定义图标3
        },
        videoSrc: videoSrc,//video图片
        posSrc: posSrc,
        studyList: '',
        exerciseList: false,
        testList: false
      }

    },
    mounted: function () {
      this.getSid();
    },
    created() {
      this.getTest();
    },
    methods: {
      getSid() {
        var _this = this;
        this.http.post('/api/v1/Course/get_chapter_info', {id: this.$route.query.id}).then((res) => {
          console.log(res);
          _this.studyList = res.data.data;
        });
      },
      geSt() {
        if (!this.exerciseList && !this.testList) {
          Toast("你已经在当前页面了");
        }
      },
      goSo() {
        this.$router.push({
          path: 'practice',
          query: {id: this.$route.query.id},

        })
      },
      goTe() {
        this.$router.push({
          path: 'test',
          query: {id: this.$route.query.id},

        })
      },
      getTest() {
        var _this = this;
        this.http.post("/api/v1/Course/get_chapter_test", {
          id: this.$route.query.id
        }).then(res => {
          this.testList = res.data.data.list.length == 0 ? false : true;
        });
        this.http.post('/api/v1/Course/get_chapter_exercise', {id: this.$route.query.id}).then((res) => {
          if (res.code == 404) {
          } else {
            _this.exerciseList = res.data.data.length == 0 ? false : true;
          }
        })
      }
    }
  };
</script>

<style lang="stylus" type="text/stylus">
  @import "study.styl";

</style>



