<template>
  <div class="mine">
    <div class="head">
      <div class="wrap">
        <div class="card">
          <div class="top">
            <div class="fl avatar"><img style="width: 100%;height:100%;position: relative;"
                                        :src="data.userinfo.headimgurl" alt=""></div>
            <div class="fl name"><span>{{data.userinfo.name}}</span></div>
            <div class="fr auth" @click="certification">实名认证</div>
            <div class="clear"></div>
          </div>
          <div class="bottom">
            <van-row>
              <van-col span="8">
                <h2>{{data.userinfo.studytime}}天</h2>
                <p>累计学习</p>
              </van-col>
              <van-col span="8">
                <h2>{{data.userinfo.curriculum}}节</h2>
                <p>累计完成课程</p>
              </van-col>
              <van-col span="8">
                <h2>{{data.userinfo.review_num}}节</h2>
                <p>获得点评</p>
              </van-col>
            </van-row>
          </div>
        </div>
      </div>
    </div>
    <div class="tab">
      <van-cell-group>
        <van-cell is-link @click="myMessage">
          <template slot="title">
            <span class="van-cell-text">我的消息</span>
          </template>
          <template slot="icon">
            <img class="width91" src="@/assets/images/xiaoxi.png" alt="">
          </template>
        </van-cell>
        <van-cell is-link @click="myApproval">
          <template slot="title">
            <span class="van-cell-text">我的审批</span>
          </template>
          <template slot="icon">
            <img class="width91" src="@/assets/images/shenpi.png" alt="">
          </template>
        </van-cell>
        <van-cell is-link to="/collection">
          <template slot="title">
            <span class="van-cell-text">我的收藏</span>
          </template>
          <template slot="icon">
            <img class="width91" src="@/assets/images/wodeshoucang.png" alt="">
          </template>
        </van-cell>
        <van-cell is-link to="/task">
          <template slot="title">
            <span class="van-cell-text">我的作业</span>
          </template>
          <template slot="icon">
            <img class="width92" src="@/assets/images/xuexi.png" alt="">
          </template>
        </van-cell>
        <van-cell :value="'本周排名第'+data.rank_no+'名'" is-link to="/rank">
          <template slot="title">
            <span class="van-cell-text">我的排名</span>
          </template>
          <template slot="icon">
            <img class="width93" src="@/assets/images/paihang.png" alt="">
          </template>

        </van-cell>

      </van-cell-group>
    </div>
    <div class="foot">
      <h2>我的学习</h2>
      <div class="rili">
        <vue-event-calendar :events="demoEvents" @monthChanged="" @dayChanged=""></vue-event-calendar>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        data: {
          userinfo: ''
        },
        demoEvents: []
      };
    },
    components: {},
    created() {
    },
    computed: {},
    mounted() {
      this.getdata()
    },
    methods: {
      getdata() {
        this.http.get('/api/v1/User/user_center').then((rs) => {
          this.data = rs.data.data;
          for (var i = 0; i < this.data.study_times.length; i++) {
            var json = {
              date: this.data.study_times[i]
            };
            this.demoEvents.push(json)
          }
        })
      },
      certification() {
        window.location.href = 'https://skylarkly.com/namespaces/200/dashboard/me';
      },
      myMessage() {
        window.location.href = 'https://skylarkly.com/namespaces/200/receipts';
      },
      myApproval() {
        window.location.href = 'https://skylarkly.com/namespaces/200/yet_another_workflow/flows';
      }
    }
  };
</script>
<style lang="less" scoped>

  .auth {
    margin-top: .3rem;
    width: 3.6rem;
    height: 1.3rem;
    border-radius: 3px;
    border: solid 0.05rem #4fdaa9;
    font-family: PingFang-SC-Medium;
    font-size: 0.7rem;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0rem;
    line-height: 1.3rem;
    color: #4fdaa9;
  }

  .rili {
    margin-top: 1rem;
    background: #fff;
    border-radius: 10px;
    padding-bottom: 2rem;
  }

  .foot {
    padding: 0 0.6rem;
    margin-top: 1.5rem;
    text-align: left;
    h2 {
      font-family: PingFang-SC-Bold;
      font-size: 0.75rem;
      font-weight: normal;
      font-stretch: normal;
      line-height: 1.1rem;
      letter-spacing: 0rem;
      color: #333333;
    }
  }

  * {
    box-sizing: border-box;
  }

  .head {
    height: 10.3rem;
    .wrap {
      width: 100%;
      height: 5rem;
      background-image: linear-gradient(45deg, #4fdaa7 0%, #58dad8 100%);
      box-shadow: 0.07rem 0.11rem 0.25rem 0rem rgba(204, 204, 204, 0.3);
      position: relative;
      .card {
        width: calc(100% - 1.2rem);
        background-color: #ffffff;
        box-shadow: 0rem 0.25rem 0.5rem 0rem rgba(84, 218, 197, 0.1);
        border-radius: 0.5rem;
        position: absolute;
        left: 0.6rem;
        top: 2.65rem;
        padding: 0.75rem 0.55rem;
        .avatar {
          width: 2rem;
          height: 2rem;
          background-color: #00a0e9;
          border-radius: 50%;
        }
        .name {
          margin-left: 0.6rem;
          line-height: 2rem;
        }
        .bottom {
          margin-top: 1.28rem;
          h2 {
            font-family: PingFang-SC-Bold;
            font-size: 0.9rem;
            font-weight: normal;
            font-stretch: normal;
            line-height: 1.8rem;
            letter-spacing: 0rem;
            color: #333333;
            text-align: center;
          }
          p {
            font-family: PingFang-SC-Medium;
            font-size: 0.7rem;
            font-weight: normal;
            letter-spacing: 0rem;
            color: #999999;
            text-align: center;
          }
        }
      }
    }
  }

  .width91 {
    width: 0.9rem;
    height: 0.8rem;
    position: relative;
    top: 0.2rem;
    margin-right: 0.8rem;
  }

  .width92 {
    width: 0.8rem;
    height: 0.9rem;
    position: relative;
    top: 0.2rem;
    margin-right: 0.8rem;
  }

  .width93 {
    width: 0.9rem;
    height: 0.83rem;
    position: relative;
    top: 0.2rem;
    margin-right: 0.8rem;
  }

  .tab {
    padding: 0 0.6rem;
    margin-top: 0.83rem;
  }

  .van-cell-group {
    border-radius: 10px;
    overflow: hidden;
    text-align: left;
  }

  .van-cell:not(:last-child)::after,
  .van-cell:not(:last-child)::after {
    left: 0;
  }

  .is-today {
    background-color: #58dad8;;
    position: absolute;
    left: 50%;
    top: 50%;
    width: 1.5rem;
    height: 1.5rem;
    margin: -0.75rem;
  }
</style>



