<template>
  <div class="rank">
    <van-tabs v-model="active">
      <van-tab title="周榜">
        <div class="content">
          <div class="card" v-if="one.length!=0">
            <div class="fl left">
              <img src="@/assets/images/img_jiangbei.png" alt="">
            </div>
            <div class="fr right">
              <div class="fl avatar">
                <img src="@/assets/images/img_jiangbei.png" alt="">
              </div>
              <div class="fl">
                <h2>TOP{{user0.rank_no}}</h2>
                <p>累计完成{{user0.num}}节课</p>
              </div>
              <div class="clear"></div>
            </div>
            <div class="clear"></div>
          </div>
          <ul v-if="one.length!=0">
            <li v-for="(i,k1) in one " :key="k1">
              <div class="fl no">TOP{{i.rank_no}}</div>
              <div class="fl avatar"><img src="@/assets/images/img_jiangbei.png" alt=""></div>
              <div class="fl name">{{i.name}}</div>
              <div class="fr text">累计完成{{i.num}}节课</div>
              <div class="clear"></div>
            </li>

          </ul>
          <div v-if="one.length==0" class=" flex-container flex-jus-c align-items flex-direction-col ">
            <div class="homeImg"><img :src="zwt"/></div>
            <p class="text-center font15 color-99">暂时还没有相关信息哦~</p>
          </div>
        </div>
      </van-tab>
      <van-tab title="月榜">
        <div class="content">
          <div class="card" v-if="two.length!=0">
            <div class="fl left">
              <img src="@/assets/images/img_jiangbei.png" alt="">
            </div>
            <div class="fr right">
              <div class="fl avatar">
                <img src="@/assets/images/img_jiangbei.png" alt="">
              </div>
              <div class="fl">
                <h2>TOP{{user1.rank_no}}</h2>
                <p>累计完成{{user1.num}}节课</p>
              </div>
              <div class="clear"></div>
            </div>
            <div class="clear"></div>
          </div>
          <ul v-if="two.length!=0">
            <li v-for="(i,k2) in two " :key="k2">
              <div class="fl no">TOP{{i.rank_no}}</div>
              <div class="fl avatar"><img src="@/assets/images/img_jiangbei.png" alt=""></div>
              <div class="fl name">{{i.name}}</div>
              <div class="fr text">累计完成{{i.num}}节课</div>
              <div class="clear"></div>
            </li>
          </ul>
          <div v-if="two.length==0" class=" flex-container flex-jus-c align-items flex-direction-col ">
            <div class="homeImg"><img :src="zwt"/></div>
            <p class="text-center font15 color-99">暂时还没有相关信息哦~</p>
          </div>
        </div>
      </van-tab>
      <van-tab title="日榜">
        <div class="content">
          <div class="card" v-if="three.length!=0">
            <div class="fl left">
              <img src="@/assets/images/img_jiangbei.png" alt="">
            </div>
            <div class="fr right">
              <div class="fl avatar">
                <img src="@/assets/images/img_jiangbei.png" alt="">
              </div>
              <div class="fl">
                <h2>TOP{{user2.rank_no}}</h2>
                <p>累计完成{{user2.num}}节课</p>
              </div>
              <div class="clear"></div>
            </div>
            <div class="clear"></div>
          </div>
          <ul v-if="three.length!=0">
            <li v-for="(i,k3) in one " :key="k3">
              <div class="fl no">TOP{{c.rank_no}}</div>
              <div class="fl avatar"><img src="@/assets/images/img_jiangbei.png" alt=""></div>
              <div class="fl name">{{c.name}}</div>
              <div class="fr text">累计完成{{c.num}}节课</div>
              <div class="clear"></div>
            </li>
          </ul>
          <div v-if="three.length==0" class=" flex-container flex-jus-c align-items flex-direction-col ">
            <div class="homeImg"><img :src="zwt"/></div>
            <p class="text-center font15 color-99">暂时还没有相关信息哦~</p>
          </div>

        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
  import zwt from "@/assets/images/wx.png";
  import {Toast} from "vant";

  export default {
    data() {
      return {
        active: 0,
        one: [],
        zwt: zwt,
        two: [],
        three: [],
        user0: [],
        user1: [],
        user2: []
      };
    },
    components: {},
    created() {
      this.getdata();
    },
    computed: {},
    mounted() {
    },
    methods: {
      getdata() {
        this.http
          .post("/api/v1/User/get_rank_list", {
            type: 0
          })
          .then(rr1 => {
            if (rr1.data.code == 200) {
              this.user0 = rr1.data.data.user_ranking;
              this.one = rr1.data.data.total_list;
              console.log(this.one);
            }
          });
        this.http
          .post("/api/v1/User/get_rank_list", {
            type: 1
          })
          .then(rr2 => {
            if (rr2.data.code == 200) {
              this.user1 = rr2.data.data.user_ranking;
              this.two = rr2.data.data.total_list;
            }
          });
        this.http
          .post("/api/v1/User/get_rank_list", {
            type: 2
          })
          .then(rr3 => {
            if (rr3.data.code == 200) {
              this.user2 = rr3.data.data.user_ranking;
              this.three = rr3.data.data.total_list;
              console.log(this.three);
            }
          });
      }
    }
  };
</script>

<style lang="less" scoped>
  .card {
    width: 100%;
    background-color: #ffffff;
    box-shadow: 0rem 0.25rem 0.5rem 0rem rgba(84, 218, 197, 0.1);
    border-radius: 0.5rem;

    .left {
      padding: 0.83rem 0rem 0.78rem 1.68rem;
      position: relative;
      img {
        width: 3.4rem;
        height: 3.4rem;
        margin-left: -1.7rem;
        margin-top: -1.7rem;
        position: absolute;
        top: 50%;
        left: 50%;
      }
    }

    .left::after {
      content: " ";
      width: 1px;
      height: 3.4rem;
      display: inline-block;
      background-color: #e5e5e5;
      margin-left: 1.73rem;
    }

    .right {
      padding-top: 1.48rem;
      padding-right: 2.58rem;
      .avatar {
        margin-right: 0.6rem;

        img {
          width: 2.2rem;
          height: 2.2rem;
          border-radius: 50%;
        }
      }

      h2 {
        font-family: PingFang-SC-Bold;
        font-size: 0.9rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0rem;
        line-height: 0.9rem;
        color: #333333;
        text-align: left;
      }

      p {
        font-family: PingFang-SC-Medium;
        font-size: 0.6rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0rem;
        color: #333333;
        margin-top: 0.55rem;
      }
    }
  }

  .content {
    padding: 0.73rem 0.6rem;
  }

  * {
    box-sizing: border-box;
  }

  ul {
    background: #fff;
    margin-top: 0.7rem;
    border-radius: 10px;

    li {
      border-bottom: 1px solid #e5e5e5;
      padding: 0 0.7rem;
      line-height: 3.53rem;
      height: 3.53rem;
      width: 100%;

      .no {
        font-family: PingFang-SC-Bold;
        font-size: 0.75rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0rem;
        color: #4fdaa9;
        margin-right: 1.05rem;
      }

      .avatar {
        margin-top: 0.65rem;
        width: 2.2rem;
        height: 2.2rem;
        margin-right: 0.93rem;

        img {
          width: 100%;
          height: 100%;
        }
      }

      .name {
        font-family: PingFang-SC-Medium;
        font-size: 0.7rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0rem;
        color: #333333;
      }

      .text {
        font-family: PingFang-SC-Medium;
        font-size: 0.7rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0rem;
        color: #333333;
      }
    }

    li:last-child {
      border: none;
    }
  }
</style>



