<template>
  <div class="comment">
    <div class="content">
      <h2>{{comList.title}}</h2>
      <span>{{comList.sub_time | formatDate}}</span>
      <strong>{{comList.state=='1'? '点评':'未点评'}} <span
        :class="comList.state=='1'? '':'hide'">{{comList.fraction}}分</span></strong>
      <p>{{comList.comment}}</p>
    </div>
  </div>
</template>
<script>
  import {formatDate} from '@/utils/date.js'

  export default {
    data() {
      return {
        comList: []
      };
    },
    components: {},
    created() {
    },
    computed: {},
    mounted() {
      this.getMent();
    },
    filters: {
      formatDate(time) {
        var date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd');
      }
    },
    methods: {
      getMent() {
        this.http.post('/api/v1/User/get_work_info', {id: this.$route.query.id}).then((res) => {
          console.log(res);
          this.comList = res.data.data;
        })
      }
    }
  }
</script>
<style lang="less" type="text/less" scoped>
  .comment {
    background: rgb(247, 249, 249);
    text-align: left;
    padding: 0.7rem 0.6rem;
    height: 100vh;
    .content {
      border-radius: 10px;
      background: #fff;
      padding: 1rem;
      h2 {
        font-size: 0.9rem;
        color: #333333;
      }
      span {
        font-size: 0.6rem;
        color: #999999;
        display: block;
        margin-top: 0.53rem;
      }
      strong {
        margin-top: 1.45rem;
        font-size: 0.75rem;
        color: #333333;
        display: block;
        span {
          float: right;
          font-size: inherit;
          font-weight: initial;
          color: #333;
          margin-top: 0;
        }
        span.hide {
          display: none;
        }
      }
      p {
        margin-top: 1.05rem;
        font-size: 0.6rem;
        font-weight: normal;
        font-stretch: normal;
        line-height: 1rem;
        color: #333333;
      }
    }
  }
</style>



