<template>
  <div id="studyaudio">
    <div class="list-chapter">
      <ul class="chapter-ul">
        <li class="chapter-ul-li">
          <audio :src="studyList.media_path" controls="controls"></audio>
          <div class="audio-title">
              <h2 class="font15 audio-h2 flex-container flex-jus-s">正文</h2>
              <article class="audio-art text-justify font14 padd-t-10">
               {{studyList.content}}
              </article>
            <div class="audio-img padd-t-10">
              <video>
                <source :src="studyList.media_path" type="video/mp4" />
                <source :src="studyList.media_path" type="video/webM" />
                <source :src="studyList.media_path" type="video/ogg" />
              </video>
            </div>
          </div>
        </li>
        <li class="chapter-ul-li">
          <h2 class="font15 audio-h2 flex-container flex-jus-s  padd-l-15 padd-r-15">生词:</h2>
          <div class="audio-img  padd-l-15 padd-r-15 flex-container flex-jus-s flex-direction-col align-items-start font14">
            <p>shoulder : n. 肩膀</p>
            <p> memories ：n. 记忆</p>
          </div>
        </li>
      </ul>

    </div>

    <van-tabbar v-model="active">
      <van-tabbar-item>
        <span>学习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active : icon.normal"
        >
      </van-tabbar-item>
      <van-tabbar-item to="practice"><span>练习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active1 : icon.normal1"
        >
      </van-tabbar-item>
      <van-tabbar-item to="test"><span>测验</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active2 : icon.normal2"
        >
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
  import avatar from '@/assets/images/banner1.jpg'
  import study from '@/assets/images/xuexi_1.png'
  import study_active from '@/assets/images/xuexi.png'
  import practice from '@/assets/images/lianxi_1.png'
  import practice_active from '@/assets/images/lainxi.png'
  import test from '@/assets/images/ceyan_1.png'
  import test_active from '@/assets/images/ceyan.png'
  import Vue from 'vue'
  import { Tabbar, TabbarItem } from 'vant'
  Vue.use(Tabbar).use(TabbarItem)
  export default {
    name: 'studyaudio',
    data(){
      return {
        active: 0,
        avatar:avatar,
        icon: {
          normal: study,
          active: study_active,//自定义图标1
          normal1: practice,
          active1: practice_active,//自定义图标2
          normal2: test,
          active2: test_active,//自定义图标3
        },
        studyList:[]
      }

    },
    mounted(){
      this.getSid();
    },
    methods: {
      getSid(){
        this.http.post('/api/v1/Course/get_chapter_info',{id:this.$route.query.id}).then((res)=>{
          console.log(res)
          this.studyList=res.data.data;

        })
      }
    }

  };
</script>

<style lang="stylus" type="text/stylus">
  @import "studyaudio.styl";

</style>



