<template>
  <div id="listdetail">
    <div class="list-banner">
      <img :src="avatar" v-if="detailList.banner_img==null || detailList.banner_img==''"/>
      <img :src="detailList.banner_img" v-else/>

    </div>
    <div class="list-title">
        <div class="list-china flex-container flex-jus-sb ">
          <div class="list-wh">
            <h2 class="font18 text-left">{{detailList.title}}</h2>
            <span class="list-span flex-container flex-jus-s font12 color-99" v-if="detailList.classify_name==null || detailList.classify_name==''">暂无相关课程</span>
            <span class="list-span flex-container flex-jus-s font12 color-99" v-else><i class="list-span-i">{{detailList.classify_name}}</i><i class="padd-l-10">{{detailList.chapter_num}}节课</i></span>

          </div>
          <div class="list-img" v-if="detailList.is_collection==1" @click="qxshoucangfun()"><img :src="shouang"/></div>
          <div class="list-img" v-if="detailList.is_collection==0" @click="shoucangfun()"><img :src="weishou" /></div>
        </div>
      <article class="list-article font14">
        {{detailList.desc}}
      </article>
      <div class="list-chapter">
          <div class="chapter-title font15 flex-container flex-jus-s">章节列表</div>
          <ul class="chapter-ul">
            <li class="chapter-ul-li" v-for="(item,index) in chapterList" @click="gostudy(item.id)" :key="index">
              <div class=" flex-container flex-jus-s align-items-cen">
                <span class="video font12" v-text="item.media_type=='video' ? '视频':item.media_type=='text' ? '文本':'音频'"></span>
                <span class="font15 padd-l-5">{{item.title}}</span>
              </div>
              <span class="iconfont icon-ren font12 color-99 flex-container flex-jus-s align-items-cen"><em class="padd-l-20">{{item.study_num}}人已学习</em></span>
              <div class="flex-container flex-jus-sb padd-t-15">
                  <span class="font12">{{item.is_time}}</span>
                  <span class="font12" :class="item.state==0 ? 'list-btn':'list-btn1'" v-text="item.state==0 ? '未完成':'已完成'"></span>
              </div>
            </li>
          </ul>


          <div class="chapter-ul flex-container  align-items flex-warp flex-jus-c  flex1" v-if="chapterList.length==0">
            <div class="homeImg"><img :src="zwt"/></div>
            <p class="text-center font15 color-99 padd-t-20">暂时还没有相关信息哦~</p>
          </div>
      </div>
    </div>

  </div>
</template>

<script>
  import Vue from 'vue'
  import avatar from '@/assets/images/banner1.jpg'
  import shouang from '@/assets/images/shouang.png'
  import weishou from '@/assets/images/shoucang.png'
  import zwt from '@/assets/images/wx.png'
  import { Toast } from "vant";
  import { Waterfall } from 'vant';
Vue.use(Waterfall);
Vue.use(Toast);
  export default {

    name: 'listdetail',

    data(){
      return {
        avatar:avatar,
        shouang:shouang,
        weishou:weishou,
        zwt:zwt,
        detailList:[],
        chapterList:[]
      }

    },
    mounted(){
    this.getId();
    },
    methods: {
    getId(){
      this.http.post('/api/v1/Course/get_course_info',{id:this.$route.query.id}).then((res)=>{
        console.log(123);
        this.detailList=res.data.data;
        this.chapterList=res.data.data.chapter_list;
      })
    },
      gostudy(id){
        this.$router.push({
          path:'study',
          title:'中国的茶文化三',
          query:{id:id},

        })


      },
       shoucangfun(){
       this.http.post('/api/v1/Course/collect_course',{c_id:this.$route.query.id}).then((res)=>{
         if(res.data.code==200){
           Toast.success('收藏成功')
           this.detailList.is_collection=1
         }
      })
    },
       qxshoucangfun(){
       this.http.post('/api/v1/Course/collect_course',{c_id:this.$route.query.id}).then((res)=>{
         if(res.data.code==200){
           Toast.success('取消收藏成功')
           this.detailList.is_collection=0
         }
      })
    },



    },


  };
</script>

<style lang="stylus" type="text/stylus">
  @import "listdetail.styl";
</style>



