<template>
  <div class="task">
    <div class="card" v-for="(i,k) in list " :key="k" @click="goread(i.id)">
      <div class="fl ">
        <img src="@/assets/images/dianping.png" alt="" v-show="i.status==2">
        <img src="@/assets/images/dianping_1.png" alt="" v-show="i.status==1">
      </div>
      <div class="fl">
        <h2>{{i.title}}({{i.state == 1 ? '已点评': '未点评'}})</h2>
        <p><span>{{i.sub_time | formatDate}}</span></p>
      </div>
      <div class="fr">
        <img src="@/assets/images/icon/you.png" alt="">
      </div>
      <div :class="i.state == 1 ? 'state red': 'state default'">{{i.state == 1 ? '已点评': '未点评'}}</div>
      <div class=" clear">
      </div>
    </div>

    <div v-if="list.length==0" class=" flex-container flex-jus-c align-items flex-direction-col height100vh">
      <div class="homeImg"><img :src="zwt"/></div>
      <p class="text-center font15 color-99">暂时还没有相关信息哦~</p>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue';
  import zwt from '@/assets/images/wx.png'
  import {Waterfall} from 'vant';

  Vue.use(Waterfall);
  import {formatDate} from '@/utils/date.js'

  export default {

    directives: {
      WaterfallLower: Waterfall('lower'),
      WaterfallUpper: Waterfall('upper')
    },
    data() {
      return {
        disabled: false,
        list: '',
        zwt: zwt,
        page: 1,
        num: 10
      };

    },
    filters: {
      formatDate(time) {
        var date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd');
      }
    },
    components: {},
    created() {
    },
    computed: {},
    mounted() {
      this.loadMore()
    },
    methods: {
      loadMore() {
        this.http.post('/api/v1/User/get_work_list', {
          intl: 10,
          p: this.page
        }).then((rr) => {
          console.log(rr)
          this.disabled = true;
          setTimeout(() => {
            if (rr.data.data.length == 10) {
              this.disabled = false;
            }
            this.list = rr.data.data
          }, 200);
        })
      },
      goread(id) {
        this.$router.push({
          path: 'comment',
          query: {id: id},

        })
      }

    }
  }
</script>
<style lang="less" type="text/less" scoped>
  * {
    box-sizing: border-box
  }

  .task {
    padding: 0.95rem 0.6rem;
    box-sizing: border-box;
  }

  .card {
    background-color: #ffffff;
    box-shadow: 0rem 0.25rem 0.5rem 0rem rgba(84, 218, 197, 0.1);
    border-radius: 10px;
    padding: 1.02rem 0.57rem 0.75rem 1.1rem;
    margin-bottom: 0.63rem;
    .fl {
      img {
        width: 1.5rem;
        margin-top: 0.4rem;
        margin-right: 1.25rem;
      }
      h2 {
        font-family: PingFang-SC-Medium;
        font-size: 0.75rem;
        font-weight: normal;
        font-stretch: normal;
        line-height: 0.8rem;
        letter-spacing: 0rem;
        color: #333333;
        text-align: left;
      }
      p {
        text-align: left;
        font-family: PingFang-SC-Medium;
        font-size: 0.6rem;
        font-weight: normal;
        font-stretch: normal;
        line-height: 0.8rem;
        letter-spacing: 0rem;
        color: #999999;
        margin-top: 0.53rem;
      }
    }
    .fr {
      img {
        width: 0.45rem;
        margin-top: 0.5rem;
        height: inherit;
        position: relative;
      }
    }
    .state {
      float: right;
      padding-right: 2em;
      margin-top: 1.33rem;
      font-size: 0.6rem;
      color: #999;
    }
    .state.red {
      color: #f00;
    }
    .state.default {
      color: #999;
    }
  }
</style>



